



































































export default {
	data() {
		return {
			// 续租时的id
			xu_id:'',
			// 选择状态
			status:'day',
			price:0,
			dc_id:'',
			day:0,
			month:0,
			qu:0,
			hyear:0,
			year:0,
			// 遮罩层数据
			maskStatus:false,
			num:1,
		};
	},
	methods:{
	// 遮罩层确定和取消
		maskClick(e){
			var that = this;
			var id = e.currentTarget.dataset.id;
			if(id==1){
				// 确认支付
				that.maskStatus = !that.maskStatus;
				var arr = {
					xu_id:that.xu_id,	//续租id
					type:2,	//租金
					pay_type:1,	//余额支付
					money:that.price,	//最后金额
					zu_time:that.status	//租期
				}
				arr = JSON.stringify(arr)
				uni.navigateTo({
					url:'../pw/pw?arr='+arr
				})
			}else if(id==2){
				that.maskStatus = !that.maskStatus;
			}
		},
		change(str){
			if(str=='day'){
				this.status = 'day'
				this.price = this.day
			}else if(str == 'month'){
				this.status = 'month'
				this.price = this.month
			}else if(str == 'qu'){
				this.status = 'qu'
				this.price = this.qu
			}else if(str == 'hyear'){
				this.status = 'hyear'
				this.price = this.hyear
			}else if(str == 'year'){
				this.status = 'year'
				this.price = this.year
			}
		}
	},
	onShow(){
		// 请求租用期限
		uni.request({
			url:this.http+'index/getDcInfo',
			method:'POST',
			header:this.header,
			data:{
				dc_id:uni.getStorageSync('dcId'),
			},
			success: (res) => {
				console.log(res)
				this.day = res.data.data.rent_day
				this.month = res.data.data.rent_month
				this.qu = res.data.data.rent_quarter
				this.hyear = res.data.data.rent_byear
				this.year = res.data.data.rent_year
				// 默认租用时间为1天
				this.change('day');
			},
			fail() {
				uni.showToast({
					title:'网络延迟，请刷新后重试!',
					icon:'none'
				})
			}
		})
	},
	onLoad(option) {
		var pages = getCurrentPages(); //当前页
		var beforePage = pages[pages.length - 2]; //上个页面
		if(beforePage.route == 'pages/userPages/yuSuccess/yuSuccess'){
			// 续租时存放的id
			this.xu_id = option.id
		}
	}
}
